import moment from "moment";
import { mapMutations } from "vuex";

const scrollQuery = [
    {
        query: "(max-width: 1200px)",
        perc: "-10%",
    },
    {
        query: "(max-width: 1300px)",
        perc: "-12%",
    },
    {
        query: "(max-width: 1500px)",
        perc: "-14%",
    },
    {
        query: "(max-width: 1600px)",
        perc: "-17%",
    },
    {
        query: "(max-width: 1900px)",
        perc: "-22%",
    },
    {
        query: "(max-width: 2100px)",
        perc: "-25%",
    },
    {
        query: "(min-width: 2000px)",
        perc: "-30%",
    },
]
export default {
    methods: {
        ...mapMutations("calendar", ['UPDATE_DAY']),
        scrollByCenter() {
            setTimeout(() => {
                const widthBox = document.querySelector(".box-calendar").offsetWidth;
                const numberBoxs = document
                    .querySelector(".currentDay")
                    .getAttribute("number");
                const centered = Math.ceil(numberBoxs / 2) + 1;
                this.$root.$emit("scrollto-vs", { x: centered * widthBox }, 500);
            });
        },
        setToday() {
            this.UPDATE_DAY(moment());
            setTimeout(() => {
                if (this.viewType == "month") {
                    this.scrollByCenter();
                }
            });
        },
    }
}